

main {
	form {
		width: 1000px;
		table {
			width: 100%;
			thead, tbody { width: 100%; }
			thead {
				font-weight: 600;
			}
			tr {
				line-height: 40px;
				td {
					width: 8%;
				}
			}
			/* tfoot {
				tr {
					background: #ffc;
					td {
						width: 100%;
					}
				}
			} */
		}
		fieldset {
			margin: 0;
			h4 {
				margin: 10px;
				padding-left: 5px;
			}
			input {
				margin-right: 25px;
				position: relative;
				top: 1px;
			}
			label { padding: 10px; }
		}
	}
}